@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: none;
    outline: none;
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}

:root {
    --bg-color: #080808;
    --second-bg-color: #001005;
    --text-color: #FFFF;
    --main-color: #00FF51;
}

body {
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: "Poppins", sans-serif;
}

.header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    padding: 1.5rem 2rem; 
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(15px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 100;
    transition: background-color 0.3s ease;
}

.logo {
    font-size: 2.5rem; 
    background: linear-gradient(to right, var(--main-color), #FFFF); 
    -webkit-background-clip: text; 
    -webkit-text-fill-color: transparent; 
    font-weight: 800;
    transition: transform 0.3s ease;
    cursor: pointer;
}

.logo:hover {
    transform: scale(1.1);
}

.menu-container {
    display: none;
}

.navbar {
    display: flex;
    margin-left: auto;
    transition: opacity 0.3s ease;
}

.navbar a {
  font-size: 1.8rem;
  color: var(--text-color);
  margin: 0 1.5rem; 
  font-weight: 500;
  border-bottom: 3px solid transparent;
  transition: color 0.3s ease, border-bottom 0.3s ease, background-color 0.3s ease, padding 0.3s ease;
}

.navbar a:hover {
  border-bottom: 3px solid var(--main-color);
  color: var(--bg-color);
  background-color: var(--main-color); 
  padding: 0.5rem 1rem; 
  border-radius: 5px; 
}







@media (max-width: 767px) {
    .navbar {
        flex-direction: column;
        display: none;
    }

    .navbar.active {
        display: flex;
    }

    .menu-container {
        display: block;
    }
}

.home {
    background: var(--bg-color);
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.home-content h1 {
  font-size: 3rem; 
  background: linear-gradient(to right, var(--main-color), #FFFF);
  -webkit-background-clip: text; 
  -webkit-text-fill-color: transparent; 
  font-weight: 800;
  transition: transform 0.3s ease;
  cursor: pointer;
}


.home-content h1:hover {
    transform: scale(1.1);
}

.btn {
    background-color: var(--main-color);
    color: var(--text-color);
    border: none;
    padding: 1.2rem 2.5rem; 
    font-size: 1.6rem;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    border-radius: 10px;
}

.btn:hover {
    background-color: var(--text-color);
    color: var(--main-color);
    transform: scale(1.05);
}

.about {
    background: var(--second-bg-color);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem; /* Added padding */
}

.about h1 {
    text-align: center;
    margin: 20px 0;
    font-size: 2.4rem; 
    color: var(--text-color);
    font-weight: 800;
}

.about-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2rem;
}

.about-column {
    flex-basis: 30%;
    background-color: var(--bg-color);
    padding: 2rem;
    margin: 1rem;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); 
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-column:hover {
    transform: scale(1.05); 
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.about-column img {
    width: 100%;
    height: auto;
    max-height: 250px; 
    object-fit: cover;
    border-radius: 10px 10px 0 0;
}

.about-column h2 {
    font-size: 1.8rem;
    color: var(--text-color);
    font-weight: 800;
    margin-top: 1rem;
}

.about-column p {
    font-size: 1.4rem; 
    color: var(--text-color);
    margin-bottom: 2rem;
}

.contact {
    background: var(--bg-color);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.contact h1 {
    text-align: center;
    margin: 20px 0;
    font-size: 2rem; 
    color: var(--text-color);
    font-weight: 800;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 600px;
    padding: 2rem;
    background-color: var(--second-bg-color);
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); 
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 1.2rem; 
    margin-bottom: 1rem;
    border: 1px solid var(--main-color);
    border-radius: 10px;
    font-size: 1.5rem;
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: "Poppins", sans-serif; /* Ensures consistent font */
}

.contact-form input:focus,
.contact-form textarea:focus {
    border-color: var(--main-color);
    box-shadow: 0 0 5px var(--main-color);
    outline: none; 
}


.contact-form textarea {
    resize: vertical; 
    min-height: 150px; 
}


.contact-form button[type="submit"] {
  background-color: var(--main-color);
  color: var(--text-color);
  border: none;
  padding: 1.2rem 2.5rem; 
  font-size: 1.8rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  border-radius: 10px;
  margin: 1rem auto; /* Centering the button */
  display: block; /* Ensures it behaves like a block element */
}


.contact-form button[type="submit"]:hover {
    background-color: var(--bg-color);
    color: var(--main-color);
    transform: scale(1.05);
}

.footer {
    background: var(--main-color);
    padding: 1rem;
    text-align: center;
    color: var(--text-color);
}

.footer h2 {
    font-size: 1.5rem;
    margin: 1rem 0;
}

.footer p {
  font-size: 1.4rem; 
  margin: 0.5rem 0; 
}

.footer a {
  text-decoration: underline;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: #080808; /* Change this color for hover effect */
}



@media (max-width: 767px) {
    .about-column {
        flex-basis: 90%; 
        margin: 1rem auto; 
    }

    .contact-form {
        padding: 1.5rem; 
    }

    .navbar a {
        font-size: 1.6rem;
        margin: 0.5rem 0; 
    }
}
